.grid {
	display: flex;
	flex-direction: row;
	width: 100%;
	flex-wrap: wrap;
	align-items: flex-start;
	padding: 0 120px;
	max-width: 1440px;

	&--lines {
		position: absolute;
		top: 0;
		width: 100%;
		height: 100%;
		left: auto;
		opacity: 0.06;
		pointer-events: none;
		// display: none;
		// mix-blend-mode: difference;
	}

	&__line {
		color: rgba($brand-black, 1);
		border-right: solid 1px;
		height: 100%;

		&:last-child {
			border: none;
		}

		// transform: scaleY(0);
		// animation: drawIn $duration-slow $duration-slow $easing1;
		// animation-fill-mode: forwards;
		// transform-origin: top;

		// @for $i from 1 through 4 {
		// 	&:nth-child(#{$i}n + #{$i}) {
		// 		animation-delay: (#{($i * 0.09)}s);
		// 	}
		// }

		// @keyframes drawIn {
		// 	0% {
		// 		transform: scaleY(0);
		// 	}
		// 	100% {
		// 		transform: scaleY(1);
		// 	}
		// }
	}

	&__row {
		display: flex;
		margin-bottom: 48px;
		flex-wrap: wrap;
		min-width: 100%;
		width: 100%;
	}


	&__item {
		display: flex;
		flex-direction: column;

		&--col-1 {
			min-width: 8.33%;
			width: 8.33%;
		}
		&--col-2 {
			min-width: 16.66%;
			width: 16.66%;
		}
		&--col-3 {
			min-width: 25%;
			width: 25%;
		}
		&--col-4 {
			min-width: 33.33%;
			width: 33.33%;
		}
		&--col-5 {
			min-width: 41.66%;
			width: 41.66%;
		}
		&--col-6 {
			min-width: 50%;
			width: 50%;
		}
		&--col-7 {
			min-width: 58.33%;
			width: 58.33%;
		}
		&--col-8 {
			min-width: 66.66%;
			width: 66.66%;
		}
		&--col-9 {
			min-width: 75%;
			width: 75%;
		}
		&--col-10 {
			min-width: 83.33%;
			width: 83.33%;
		}
		&--col-11 {
			min-width: 91.66%;
			width: 91.66%;
		}
		&--col-12 {
			min-width: 100%;
			width: 100%;
		}
	}
}