@keyframes drawOutInRight {
  0%, 20% {
    transform-origin: right;
    transform: scaleX(1); }
  40% {
    transform-origin: right;
    transform: scaleX(0); }
  60% {
    transform: scaleX(0);
    transform-origin: left; }
  80%, 100% {
    transform: scaleX(1);
    transform-origin: left; } }

.text-link {
  position: relative;
  display: inline-flex;
  flex-direction: column; }
  .text-link__line-container {
    height: 1px;
    width: 100%;
    display: block;
    overflow: hidden;
    background: rgba(19, 17, 18, 0.24); }
  .text-link__line {
    background: #131112;
    height: 100%;
    width: 100%;
    display: block;
    transform: scaleX(0);
    transform-origin: right;
    transition: transform cubic-bezier(0.19, 1, 0.22, 1) 0.9s; }
  .scrolling-section--black .text-link .text-link__line-container {
    background: rgba(254, 254, 254, 0.24); }
  .scrolling-section--black .text-link .text-link__line {
    background: #fefefe; }
  .text-link.text-link--hide-underline .text-link__line-container {
    background: transparent; }
  .text-link--hovered .text-link__line {
    transform: scaleX(1);
    transform-origin: left; }

